@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@700&display=swap');

body {
    margin: 0;
    font-family: Arial, sans-serif;
}

.App {
    text-align: center;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.progress {
    position: fixed;
    top: 10px;
    left: 10px;
    display: flex;
    gap: 15px;
    font-size: 20px;
}

.type {
    cursor: pointer;
    opacity: 0.7;
    transition: opacity 0.2s ease;
}

.type.active {
    font-weight: bold;
    opacity: 1;
}

.character-display {
    font-size: 120px;
    font-family: "Noto Sans JP", sans-serif;
    transition: transform 0.3s ease-in-out;
}

.pronunciation {
    font-size: 40px;
    margin-top: 20px;
    color: gray;
}
